import { GET_CHAMPS, SEARCH_CHAMPS } from "./types";
import champions from "../json/champions";

export const getChamps = () => dispatch => {
  dispatch({
    type: GET_CHAMPS,
    payload: champions
  });
};
export const searchChamps = data => dispatch => {
  dispatch({
    type: SEARCH_CHAMPS,
    payload: data
  });
};
